<template>
    <div class="setView">
        <div class="setTop">
            <span>当前位置:
                <i>设置</i>
            </span>
        </div>
        <div class="setContent clearfix">
            <div class="setLeft">
                <div v-for="item in perInfor">
                    <div class="kuang">
                        <h1>个人信息</h1>
                    </div>
                    <div class="kuang">
                        姓名:<input type="text" readonly="readonly" :value=item.name>
                    </div>
                    <div class="kuang">
                        所属地区:<input type="text" readonly="readonly" :value=item.section_one>
                    </div>
                    <div class="kuang">
                        所属部门:<input type="text" readonly="readonly" :value=item.section_two>
                    </div>
                </div>
            </div>
            <div class="setRight">
                <div class="topImg">
                    <el-upload
                        class="avatar-uploader"
                        action="https://jsonplaceholder.typicode.com/posts/"
                        :show-file-list="false"
                        :on-success="handleAvatarSuccess"
                        :before-upload="beforeAvatarUpload">
                        <img v-if="imageUrl" :src="imageUrl" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                    <p>{{name}}</p>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data(){
            return{
                imageUrl: '',
                name:'',
                perInfor:'',//人员基本信息
            }
        },
        created(){
            this.$store.state.showHeader = '5';
            this.getInfor();
        },
        methods: {
            getInfor(){
                this.$axios.post('/api/user-list',{
                  "id":this.$route.params.id
                }).then((res)=> {
//                    console.log(res)
                    this.perInfor=res.data.data.data;
                    this.name=res.data.data.data[0].name;
                }).catch((error)=> {
                    console.log(error)
                })
            },

            handleAvatarSuccess(res, file) {
                this.imageUrl = URL.createObjectURL(file.raw);
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isJPG) {
                    this.$message.error('上传头像图片只能是 JPG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传头像图片大小不能超过 2MB!');
                }
                return isJPG && isLt2M;
            }
        }
    }
</script>
<style lang="less" rel="stylesheet/less">
  @import '../../../static/css/base.css';
  .setView{
    margin: 0 auto;
    width: 1280px;
    .setTop{
      width: 100%;
      height: 40px;
      line-height: 40px;
      background-color: #f1f1f1;
      margin-bottom: 23px;
      span{
        margin-left: 80px;
        i{
          color:#4a90e2;
          cursor: pointer;
        }
      }
    }
    .setContent{
      // width: 100%;
      height: 100%;
      margin: 0 auto;
      background-color: #f1f1f1;
      .setLeft{
        margin-left: 60px;
        padding-top: 20px;
        width: 900px;
        float: left;
        .kuang{
          margin-top: 20px;
          width: 893px;
          height: 45px;
          line-height: 45px;
          background-color:white;
          margin-bottom: 25px;
          text-indent:25px;
          h1{
            line-height: 45px;
            color: #f5a623;
            text-indent:25px;
          }
          h2{
            line-height: 45px;
            color: #f5a623;
            text-indent:25px;
            margin-top:40px;
          }
          input{
            outline:none;
            float: right;
            width: 554px;
            height: 40px;
            span{
              outline:none;
              float: right;
              width: 554px;
              height: 40px;
            }
          }
        }
        button{
          width: 283.6px;
          height: 42px;
          border-radius: 3px;
          background-color: #4a90e2;
          cursor: pointer;
          color:white;
          margin:148px 0  36px 339px;
          outline:none;
          position: relative;
          top:-40px;
        }
      }
      .setRight{
        margin-right: 50px;
        float: right;
        .topImg{
          width: 250px;
          height: 239px;
          background-color: #ffffff;
          margin: 40px 0 21px 0;
          box-sizing:border-box;
          .avatar-uploader .el-upload {
            border: 1px dashed #d9d9d9;
            // width: 178px;
            border-radius: 6px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
            left: 35px;
            top: 10px;

          }
          .avatar-uploader .el-upload:hover {
            border-color: #409EFF;
          }
          .avatar-uploader-icon {
            font-size: 28px;
            color: #8c939d;
            width: 178px;
            height: 178px;
            line-height: 178px;
            text-align: center;
          }
          .avatar {
            width: 178px;
            height: 178px;
            display: block;
          }
          p{
            font-size: 14px;
            text-align: center;
            color: #4a4a4a;
            margin-top: 10px;
          }
        }
        .submit{
          button{
            width: 250px;
            height: 42px;
            border-radius: 3px;
            background-color: #4a90e2;
            outline:none;
            cursor: pointer;
            color:white;
          }
        }
      }
    }
  }
</style>



